<template>
  <div id="app">
    <el-container>
      <el-header style="text-align: right; font-size: 12px; padding: 0 100px;">
        <span class="heard-left">
          <img :src="require('./assets/logo.png')" style="height: 60px; width: 120px; background: #f6f6f6; ">
        </span>
        <span v-if="account !== null">{{ account.name }}</span>
        <i class="el-icon-setting" style="margin-right: 15px"></i>

        <el-button v-if="account === null" type="success" @click="() => init_scatter()">Login</el-button>

        <el-button type="success" @click="shareModel = true">Share</el-button>
        <el-button type="success" @click="playModel = true">How To Play</el-button>
      </el-header>
      <el-main class="app-main">
        <el-row :gutter="12">
          <el-col :span="4" v-for="(o, index) in cards" :key="index">
            <el-card class="card-box2">
              <img :src="o.img" class="image">
              <div>
                <span>{{ o.memo }} {{ o.odds}}</span>
                <div class="bottom clearfix">
                  <el-row>
                    <el-col :span="24">
                      <el-input-number size="small" v-model="o.bet" :precision="2" :step="0.1" :min="0.1"></el-input-number>
                    </el-col>
                    <el-col :span="24" style="padding: 10px 0">
                      <el-button size="small" type="success" @click="actionBet(index)">Bet</el-button>
                    </el-col>
                  </el-row>
                </div>
              </div>
            </el-card>
          </el-col>
        </el-row>

        <el-row class="table-header list-item">
          <el-col :span="5">Account</el-col>
          <el-col :span="5">Bet</el-col>
          <el-col :span="5">BetTeam</el-col>
          <el-col :span="5">WinnerTeam</el-col>
          <el-col :span="4">Payout</el-col>
        </el-row>
        <transition-group name="fadeLeft" tag="p">
          <div v-for="(item, index) in hisotry" v-bind:key="index" class="list-item">
            <transition enter-active-class="bounce bounceInDown">
              <el-row :class="'list-item ' + tableRowClassName(index)">
                <el-col class="list-row" :span="5">{{ item.account }}</el-col>
                <el-col class="list-row" :span="5">{{ item.bet / 10000 }} EOS</el-col>
                <el-col class="list-row" :span="5">{{ item.betteam }}</el-col>
                <el-col class="list-row" :span="5">{{ item.winnerteam }}</el-col>
                <el-col class="list-row" :span="4">{{ item.payout ? (item.payout / 10000) + ' EOS' : '' }}</el-col>
              </el-row>
            </transition>
          </div>
        </transition-group>
      </el-main>
    </el-container>

    <el-dialog :visible.sync="shareModel" width="50%">
      <span>
        {{ domain }}?s={{ account !== null ? account.name : '' }}
      </span>
    </el-dialog>
    <el-dialog title="How to play" :visible.sync="playModel" width="50%">
      <div style="text-align: left;width: 100%;display:block;word-break: break-all;word-wrap: break-word;">
        <ol>
          <li>
            <p>Make sure you have an EOS account. </p>
          </li>
          <li>
            <p>If you haven’t already, download and install Scatter, an EOS wallet that facilitates interaction between users and dApps.</p>
          </li>
          <li>
            <p>The red,black, heart,spade,diamond,club probabilities are 1%, 4%, 10%, 15%, 25%, 45%, and the corresponding rewards are 100X, 25X, 10X, 6.66X, 4X, 2.22X.</p>
          </li>
          <li>
            <p>If the team you choose wins, you will be rewarded!</p>
          </li>
          <li>
            <p>The table below shows the recent bets of all players worldwide.</p>
          </li>
          <li>
            <p>Of course, we will withdraw 2% fees from the principal, pay for the consumed ram, and later help the user to mortgage the cpu.</p>
          </li>
        </ol>

        <div style="padding: 10px; display: block; text-align: center;">
          <p> Still have questions? Join the rollredjoker Community!</p>

          <a target="_blank" href="https://github.com/rollgame/rollredjoker">
            <img style="width: 48px; height: 48px;" :src="require('./assets/icons8-github-48.png')">
          </a>

          <a target="_blank" href="https://t.me/rollredjoker">
            <img style="width: 48px; height: 48px;" :src="require('./assets/icons8-telegram-app-48.png')">
          </a>

        </div>

      </div>
    </el-dialog>
  </div>

</template>

<script>
import Eos from "eosjs";
import { network, options, TableApi } from "./config.js";
import { default as axios } from "axios";
export default {
  data() {
    return {
      domain: location.origin,
      scatter: null,
      requiredFields: null,
      eos: null,
      account: null,
      cards: [
        {
          img: require("./assets/1.jpg"),
          memo: "red",
          odds: "100X", // red/black/spade/heart/diamond/club
          bet: 0.01
        },
        {
          img: require("./assets/2.jpg"),
          memo: "black",
          odds: "25X",
          bet: 0.1
        },
        {
          img: require("./assets/3.jpg"),
          memo: "heart",
          odds: "10X",
          bet: 0.1
        },
        {
          img: require("./assets/4.jpg"),
          memo: "spade",
          odds: "6.66X",
          bet: 0.1
        },
        {
          img: require("./assets/5.jpg"),
          memo: "diamond",
          odds: "4X",
          bet: 0.1
        },
        {
          img: require("./assets/6.jpg"),
          memo: "club",
          odds: "2.22X",
          bet: 0.1
        }
      ],
      hisotry: [],
      time: null,
      items: [],
      shareModel: false,
      playModel: false
    };
  },
  created() {
    this.$nextTick(() => {
      document.addEventListener("scatterLoaded", scatterExtension => {
        const scatter = window.scatter;
        this.init_scatter();
      });
      this.getHistory();
    });
  },
  mounted() {
    setInterval(() => {
      this.getHistory();
    }, 1500);
  },
  methods: {
    tableRowClassName(rowIndex) {
      if (rowIndex % 2 === 0) {
        return "success-row";
      }
      return "warning-row";
    },
    getShare: function() {
      let name = "s";
      return (
        decodeURIComponent(
          (new RegExp("[?|&]" + name + "=" + "([^&;]+?)(&|#|;|$)").exec(
            location.href
          ) || [, ""])[1].replace(/\+/g, "%20")
        ) || ""
      );
    },
    upTime: function() {
      this.time = Date.parse(new Date());
    },
    transfer: function(amount, memo) {
      amount = amount || 0.1;
      memo = memo || "club";
      let s = this.getShare();
      if (s !== "") {
        memo = memo + "-" + s.trim();
      }
      amount = new Number(amount).toFixed(4);
      this.$message("pending transfer");
      this.eos
        .transfer(this.account.name, "rollredjoker", amount + " EOS", memo)
        .then(() => {
          this.$message({
            message: "Bet Success, plase check youer eos account.",
            type: "success"
          });
          this.upTime();
          this.getHistory();
        })
        .catch(err => {
          let msg = "";
          try {
            err = JSON.parse(err);
            msg = err.error.details[0].message;
          } catch (e) {
            msg = err;
          }

          this.$message.error("Bet fail:  " + msg);
          this.upTime();
          this.getHistory();
          console.log(err.toString());
        });
    },
    setIdentity: function(identity) {
      this.account = identity.accounts.find(acc => acc.blockchain === "eos");
      this.eos = scatter.eos(network, Eos, {});
      this.requiredFields = {
        accounts: [network]
      };
    },
    init_scatter: function(cb) {
      if (this.eos != null) return;
      if (!("scatter" in window)) {
        this.$message.error("need scatter extension");
      } else {
        this.scatter = scatter;
        this.scatter
          .getIdentity({
            accounts: [
              {
                chainId: network.chainId,
                blockchain: network.blockchain
              }
            ]
          })
          .then(identity => {
            this.setIdentity(identity);
            if (cb !== undefined) {
              cb();
            }
          })
          .catch(err => {
            this.$message.error("Scatter init fail " + err.message);
          });
      }
    },
    getHistory() {
      axios
        .post(TableApi, {
          json: true,
          scope: "rollredjoker",
          code: "rollredjoker",
          table: "player",
          limit: "30"
        })
        .then(res => {
          if (res.status === 200) {
            this.hisotry = res.data.rows.reverse();
          }
        });
    },
    actionBet(index) {
      if (this.eos === null) {
        this.$message.error("plase login.");
        this.init_scatter();
        return;
      } else {
        this.transfer(this.cards[index].bet, this.cards[index].memo);
      }
    },
    add: function(row) {
      this.items.splice(0, 0, row);
    },
    remove: function() {
      this.items.splice(this.items.length - 1, 1);
    }
  }
};
</script>

